"use client"

import ScrollReveal from "./ScrollReveal"
import Image from "next/image"

export default function Services() {
  const items = [
    { 
      title: "Web Development", 
      body: "Next.js, React, Node.js, API design",
      img: "/images/services/web.png"
    },
    { 
      title: "UI/UX Design", 
      body: "Design systems, prototyping, research",
      img: "/images/services/design.png"
    },
    { 
      title: "Brand Strategy", 
      body: "Identity, messaging, positioning",
      img: "/images/services/brand.png"
    },
  ]

  return (
    <section className="py-24 bg-transparent">
      <div className="container mx-auto">

        {/* Heading */}
        <ScrollReveal>
          <h2 className="text-4xl font-bold text-center text-green-400 mb-16">
            Our Services
          </h2>
        </ScrollReveal>

        {/* Service Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-10">
          {items.map((item, index) => (
            <ScrollReveal key={item.title} delay={index * 0.1}>
              <div className="
                group
                p-8 rounded-2xl 
                bg-[#111] border border-[#1f1f1f]
                shadow-lg 
                hover:shadow-green-500/25 
                hover:-translate-y-3 
                transition-all duration-300
              ">

                {/* Image */}
                <div className="relative w-full h-40 mb-6">
                  <Image
                    src={item.img}
                    alt={item.title}
                    fill
                    className="object-contain group-hover:scale-105 transition-all duration-300"
                  />
                </div>

                {/* Title */}
                <h3 className="text-xl text-white font-semibold text-center">
                  {item.title}
                </h3>

                {/* Subtitle */}
                <p className="text-gray-400 mt-3 text-center">
                  {item.body}
                </p>
              </div>
            </ScrollReveal>
          ))}
        </div>
      </div>
    </section>
  )
}
